:root{
    --primary-color: #0B2434;
    --secondary-color: #f5f5ff;
    --text-color: #2B283A;
    --btn-primary-color: #5035FF;
    --btn-held-color: #59E391;
}
*, *::before, *::after{
    box-sizing: border-box;
    font-family: "Karla", sans-serif;
}
body{
    margin: 0;
    background-color: var(--primary-color);
}
.App{
    display: flex;
    height: 100vh;
    justify-content: center;
    align-items: center;
}
.main{
    position: relative;
    width: 500px;
    height: 500px;
    background-color: var(--secondary-color);
    border-radius: 1rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 2rem;
}
.title{
    font-size: 3rem;
    margin: 0;
    font-family: 'Inter';
}
.roll-count{
    position: absolute;
    top: 1.75rem;
    right: 2.5rem;
}
.time-count{
    position: absolute;
    top: 1.75rem;
    right: 2.5rem;
}
.instructions{
    margin: 0;
    padding-inline: 2.5rem;
    font-family: 'Inter';
}
.dies{
    display: grid;
    grid-template-columns: repeat(5, 60px);
    row-gap: 1.75rem;
    column-gap: 1.75rem;
    justify-content: center;
    align-content: center;
}
.die{
    cursor: pointer;
    width: 60px;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: white;
    font-size: 2.25rem;
    font-weight: 700;
    box-shadow: 0 4px 9px 0 rgba(0,0,0,.25);
    border-radius: .75rem;
    color: var(--text-color);

    transition: transform 100ms ease-in-out;
    
    &:hover{
        transform: scale(1.09);
    }
}
.btn{
    cursor: pointer;
    border: none;
    outline: none;
    background-color: var(--btn-primary-color);
    box-shadow: 0px 4px 8px rgba(0, 0, 0, .25);
    border-radius: .25rem;
    padding: .35em 1.65em;
    font-size: 1.75rem;
    color:  var(--secondary-color);
    opacity: .8;
    transition: opacity 100ms ease-in-out;
    
    &:hover{
        opacity: 1;
    }
}
.held{
    background-color: var(--btn-held-color);
}